@tailwind base;
@tailwind components;
@tailwind utilities;

/* 自定义样式 */
.human-msg {
  background-color: #fffde7;
}
.claude-msg {
  background-color: #e3f2fd;
}
.selected-msg {
  background-color: #bbdefb !important;
}

/* 修复布局问题 */
.detail-content-container {
  width: 100%;
  height: 100%;
  overflow: auto;
}

.json-content {
  width: 100%;
  overflow-x: auto;
}

/* 确保主要内容区域不会被挤压 */
.flex-1 {
  flex: 1 1 0% !important;
}

/* 确保消息列表面板有一个固定的最小宽度 */
.message-list-panel {
  min-width: 350px;
  width: 40%;
  max-width: 500px;
  flex-shrink: 0;
}

/* JSON选项卡的特殊处理 */
.json-tab-content {
  max-width: 100%;
  overflow-x: auto !important;
  overflow-y: auto !important;
}

/* 提高JSON代码块的可读性 */
pre[class*="language-json"] {
  white-space: pre-wrap !important;
  word-break: break-word !important;
  max-width: 100% !important;
}

/* 确保右侧内容区域不会被挤压 */
.content-panel {
  min-width: 0;
  flex: 1 1 auto;
  overflow: hidden;
}

/* 修复选项卡样式 */
.tab-button {
  flex-shrink: 0;
}

/* 解决某些界面元素的固定宽度问题 */
.fixed-width-input {
  width: auto !important;
  flex: 1;
}

/* 确保代码高亮区域的限制 */
.syntax-highlighter-container {
  max-width: 100%;
  overflow-x: auto;
}